.table{border-collapse:collapse;border-spacing:30px;width:100%;overflow:hidden}.table tr{transition:.3s}.table tr:hover,.table tr:nth-child(even):hover{background-color:hsla(0,0%,80%,.2);cursor:pointer}.table tr:nth-child(even){background-color:hsla(0,0%,80%,.3)}.table td{padding:.25rem;text-align:center}.table tr:has(th){border-radius:10px;padding:1rem}.table th{padding:.25rem;text-align:center}.table tfoot{background-color:rgba(75,163,195,.3)}.table tfoot button{background-color:#4ba3c3;border-radius:50%;width:32px;aspect-ratio:1;padding:0;outline:0;border:0;color:#f4f4f8;cursor:pointer}.table tbody{position:relative;height:100px}.table tbody.rotate::after{content:"";font-family:"Font Awesome 6 Free";font-weight:900;font-size:2rem;width:700%;height:700%;background-color:rgba(15,16,32,.5);position:absolute;top:-300%;left:-300%;color:#f4f4f8;display:flex;align-items:center;justify-content:center;animation:tableLoadingAnimation 2s infinite}