.form-control{display:flex;flex-direction:column;position:relative}.form-control label{position:absolute;top:50%;left:.5rem;transform:translateY(-50%);font-weight:100;transition:.3s;color:rgba(15,16,32,.7)}.form-control label:has(+input:focus),.form-control label:has(+input:not(:placeholder-shown)){scale:.7;top:5px;left:.25rem}.dropdown{position:relative}.dropdown-label{cursor:pointer;margin-left:-5px}.dropdown-input{width:100%;cursor:pointer}.dropdown-input:not(:placeholder-shown)+.dropdown-list,.dropdown-input:active+.dropdown-list,.dropdown-input:focus+.dropdown-list{position:absolute;animation:fadeIn .3s ease-in forwards}.dropdown::after{content:"";font-family:"Font Awesome 6 Free";font-weight:900;position:absolute;right:.5rem;top:50%;transform:translateY(-50%);cursor:pointer}.dropdown:has(.dropdown-input:active)::after,.dropdown:has(.dropdown-input:focus)::after{animation:caretRotation .3s ease forwards}.dropdown-list{display:none;position:absolute;z-index:100;top:calc(.5rem + 50px);background-color:#f4f4f8;width:100%;border-radius:10px;box-shadow:0px 5px 10px rgba(0,0,0,.298)}.dropdown-list~input:not(::placeholder-shown){display:block}.dropdown-list.active{display:block}.dropdown-option{padding:.5rem;font-weight:bold;cursor:pointer}.dropdown-option:hover{background-color:hsla(0,0%,80%,.2)}.dropdown-option:nth-child(odd){background-color:hsla(0,0%,80%,.4)}